import { FileText } from 'lucide-react';

export const SourceDocumentsCard = ({ documents }: { documents: string[] }) => (
    <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
        <h3 className="text-lg font-semibold text-gray-700 flex items-center mb-3">
            <FileText className="mr-3 h-6 w-6 text-corporate-red/70"/>
            Source Documents for this Analysis
        </h3>
        {documents && documents.length > 0 ? (
            <ul className="space-y-1 text-sm text-gray-600 list-disc list-inside">
                {documents.map(doc => <li key={doc} className="truncate">{doc}</li>)}
            </ul>
        ) : <p className="text-sm italic">No source documents found for this date range.</p>}
    </div>
);